* {
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    font-style: oblique;
}

.navigation-sub {
    /* background-color: rgba(0, 134, 134, 0.52); */
    width: 100%;
    height: 40px;
    top: 15px;
    display: block;
    position: fixed;
}

#header-img {
    position: fixed;
    z-index: -1;
    top: 0px;
    width: 100%;
    height: 100%;
    display: block;
    
}

#portfolio{
    text-align: center;
    font-size: 80px;
