}

.menu {
    box-sizing: border-box;
    text-align: center;
    display: block;
    z-index: -1;
}

.item{
    color: rgb(98, 0, 255);
    font-size: 2.5ch;
    margin-left: 90px;
}

.item:hover {
    /* color: rgb(0, 18, 98); */
