  #Projects_text {
    font-size: 80px;
    color: #fff;
    text-align: center;
    animation: glow 1s ease-in-out infinite alternate;
    z-index: 0;
  }

  @keyframes glow {
    from {
      text-shadow: 0 0 10px #fff, 0 0 20px #b58efb, 0 0 30px #b58efb, 0 0 40px #b58efb, 0 0 50px #b58efb;
    }
    
    to {
      text-shadow: 0 0 20px #fff, 0 0 30px #a04dff, 0 0 40px #8b4dff, 0 0 50px #8b4dff, 0 0 60px #8b4dff;
    }
  }

  .details {
    width: 50%;
    // margin-right: %;
    height: 50px;
    margin-left: 25%;
    // border: 1px solid #333;
    // font-family: 'Cinzel', serif;
    font-size: 20px;
    // cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
    transition: 1.2s;
    animation: glow 1s ease-in-out infinite alternate;
  }
  
  .details::before, .details::after {
    position: absolute;
    // background: rgb(98, 0, 255);
    z-index: -1;
    transition: 1s;
    content: '';
  }
  
  .details::before {
    height: 100%;
    width: 100%;
  }
  
  .details:hover::before {
    width: 0px;
    background: rgba(98, 0, 255, 0.63);
  }
  
  .details:hover::after {
    height: 0px;
    background: rgba(98, 0, 255, 0.89);
  }
  
  .details:hover {
    background: rgba(98, 0, 255, 0.883);
  }